import Common from '../../assets/js/common.js';
export default{
    data(){
        return{
              ua:Common.ua,
              hasInput:false,
              inputVal:'',
              top:0,
              isFocus:false
        }
    },
    props:{
      value:[String,Number],
      placeholder:{
         type:[String],
         default:'请输入城市名'
      }
    },
    created(){
        this.inputVal=this.value;
        this.$on('scrolling',(position)=>{
           !this.top?this.top=position.top:0;
           if(Math.abs((Math.abs(position.top)-Math.abs(this.top)))>10 && this.isFocus){
               this.$refs.input.blur();
               this.top=0;
           }
        })
    },
    methods:{
        clearVal(){
           this.$refs.input.value='';
           this.inputVal=''
           this.$emit('clear',true)
        },
        handlefoucs(){
          this.isFocus=true;
          this.$emit('focus',true)
        },
        handleblur(){
          this.isFocus=false;  
          this.$emit('blur',true)
        }
    },
    watch:{
        value(newVal){
            this.inputVal=this.value;
        },
        inputVal(newVal){
          if(newVal!=''){
              this.hasInput=true;
          }
          this.$emit('input',newVal)
        },

    }
}



